.transactions_history {
  width: 600px;
  text-align: center;
  margin: 0 auto;
  border: 3px solid rgb(14, 9, 9);
  border-collapse: collapse;
}

.table_head {
  border: 2px solid rgb(35, 17, 17);
  background-color: bisque;
  font-size: 24px;
}

.table_body {
  border: 1px solid #ccc;
  background-color: rgb(128, 141, 165);
  padding-top: 12px;
  padding-bottom: 12px;
}
